@-webkit-keyframes hideshow {
  0% { opacity: 0.3; }
  25% { opacity: 0.6; }
  50% { opacity: 1; }
  75% { opacity: 0.6; }
  100% { opacity: 0.3; }
  
}

@keyframes hideshow {
  0% { opacity: 0.3; }
  25% { opacity: 0.6; }
  50% { opacity: 1; }
  75% { opacity: 0.6; }
  100% { opacity: 0.3; }
  
} 

