.text-nav-card {
  width: 150px;
  height: 150px;
  line-height: 132px;
  background-color: rgba(0,0,0,.5);
  color: #999;
  margin-top: 14px;
  transition: 0.9s;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  
}
.text-nav-card:hover {
  background-color: rgba(0,0,0,.7);
  color: white !important;  
  transform: rotateY(180deg);
}

.text-nav-card:hover>span {
  transform: unset;
}

